<template>
  <div class="showContent checkInfoContent">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
    <backSubMenu :backTitle="backTitle" :lineUnder="false"></backSubMenu>
    <div class="detail">
      <div class="left">
        <div class="infor_form_detail">
          <div>
            <!-- 公司名 -->
            <label>Name</label>
            <input type="text" :value="detailData.name" readonly>
          </div>
          <div>
            <!-- ID -->
            <label>Npwp</label>
            <input type="text" :value="detailData.npwp" readonly>
          </div>
          <div>
            <!-- kpp_name -->
            <label>kpp_name</label>
            <input type="text" :value="detailData.kpp_name" readonly>
          </div>
          <div>
            <!-- Name Similar -->
            <label>Name Similar</label>
            <input type="text" :value="detailData.nameSimilar" readonly>
          </div>
          <div>
            <!-- Kpp_info -->
            <label>Kpp_info</label>
            <input type="text" :value="detailData.kpp_info" readonly>
          </div>
          
         
        </div>
      </div>
      <div class="right">
        <div class="infor_form_detail">
          <div>
            <!-- Time -->
            <label>Time</label>
            <input type="text" :value="$util.timestampToDay(record.created_at)" readonly>
          </div>
          <!-- <div>
            <label>Event</label>
            <input type="text" :value="detailData.name" readonly>
          </div> -->
          <div class="hasDot">
            <!-- Situation -->
            <label>Situation</label>
            <input type="text" :value="record.status" readonly>
            <span :class="record.status=='success'?'greenDot':'redDot'" id="statusDot">
              <i class="iconfont amd-dot"></i>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
import backSubMenu from "@/components/backSubMenu";
import { mapGetters } from "vuex";
import { fail } from "assert";

export default {
  data() {
    return {
      routekey: [
        "npwpInfo",
        "npwpHistory;npwpHistoryDetail"
      ],
      backTitle: "Details",
      detailData: "",
      record:"",
    };
  },
  computed: {},
  components: {
    requestAndHisSubMenu,
    backSubMenu
  },
  methods: {
    getDetailData() {
      let params = this.$route.params;
      console.log(params);
      this.record=params.record;
      this.detailData = params.record.response.data;
      console.log(this.detailData);
    }
  },
  created() {
    if(this.$route.params.record){
      this.getDetailData();
    }else{
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less">
@import "../../../css/checkInfor.less";
</style>





